import React from 'react';
import dummy from '../db/product_data.json';
import { BsCart2 } from 'react-icons/bs';

function BestItem(props) {
  return (
    <>
      <article className='BestItem'>
        <h2>  </h2>
        <p>   85%  !</p>

        {/* map    */}
        <ul className='product_box'>
          {dummy.products.map(product=>(
            <li key={product.name} className='product'>
              <div className='img_box'>
                <img src={`${process.env.PUBLIC_URL}/images/${product.img}`} alt="" />
              </div>
              <div className="button-wrapper">
                <button type="button">
                  <BsCart2 size={20} color="#333" />
                </button>
              </div>
              <p>{product.name}</p>
              <p>{Number(product.price).toLocaleString()}</p>
              <div className="discount">
                <span>{product.sale*100}%</span>
                <span>{(Math.floor(Number(product.price) * (1 - product.sale) / 10) * 10).toLocaleString()}</span>
              </div>
            </li>
            ))}
        </ul>
      </article>
    </>
  );
}

export default BestItem;