import React, {useState, useEffect} from 'react';

function Banner(props) {
  //const [, ] = useState();
  const [RandomPic, setRandomPic] = useState();
  
  useEffect(()=>{
    const imgArray = [
      'banner01.jpg',
      'banner02.jpg',
      'banner03.jpg'
    ]

    let timerId; //  ID  

    const showImage = () => {
      //const imgNum = Math.floor(Math.random()*3+1);  //1~3
      const imgNum = Math.round(Math.random()*2); //1~3
      
      //   n   .
      setRandomPic(imgArray[imgNum]);
      console.log(imgArray[imgNum]);
      setTimeout(showImage, 5000);
    };

    showImage();

    return () => {
      clearTimeout(timerId);
    };
  }, []);


  return (
    <>
      <article>
        <div>
          <img src={`${process.env.PUBLIC_URL}/images/${RandomPic}`} alt=""/>
        </div>
      </article>
    </>
  );
}

export default Banner;